<template>
  <div class="all">
    <div class="item" v-for="(question,index) in questions" :key="index">
      <div class="bb">{{question.xqu}}</div>
      <div class="question">
        <div><img :src="question.ximg" alt=""></div>
        <div class="bg">6</div>
        <div class="miao">{{question.xmiao}}</div>
      </div>
      <div class="iam">
        <div>{{question.xposition}}</div>
        <div>{{question.xscore}}<span> </span></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      questions: [
        {
          ximg: require("img/20170924121925_12.png"),
          xqu: "",
          xposition: "",
          xscore: "4.1",
          xmiao: "123",
        },
        {
          ximg: require("img/20170924121925_06.png"),
          xqu: "",
          xposition: "",
          xbackground: "",
          xscore: "5",
          xmiao: "1200",
        },
        {
          ximg: require("img/20170924121925_02.png"),
          xqu: "",
          xposition: "CEO",
          xbackground: "",
          xscore: "3.2",
          xmiao: "80",
        },
        {
          ximg: require("img/20170924121925_08.png"),
          xqu: "",
          xposition: "PPP",
          xbackground: "",
          xscore: "3.9",
          xmiao: "81",
        },
        {
          ximg: require("img/20170924121925_10.png"),
          xqu: "",
          xposition: "",
          xbackground: "",
          xscore: "5",
          xmiao: "66",
        },
        {
          ximg: require("img/20170924121925_03.png"),
          xqu: "",
          xposition: "",
          xbackground: "",
          xscore: "4.9",
          xmiao: "72",
        },
      ]
    }
  },
}
</script>

<style lang="scss" scoped>
.all {
  margin-top: 80px;
  margin-bottom:100px;
  .item {
    padding: 30px;
    line-height: 28px;
    border-bottom: #c5c5c5 solid 1px;
    background: #fff;
    .bb{
      font-size: 13.69px!important;
    }
    .question {
      display: flex;
      flex-direction: row;
      margin-top: 24px;
      align-items: center;
      div:nth-of-type(1) {
        flex: 0 0 74px;
        img {
          width: 74px;
          border-radius: 50%;
        }
      }
      .bg {
        color: #fff;
        width: 174px;
        height: 60px;
        background: url(~img/green_bg.png) no-repeat;
        background-size: 100% auto;
        line-height: 60px;
        padding-left: 100px;
        margin-left: 10px;
        flex: 1;
      }
      .miao {
        text-align: right;
        flex: 0 0 200px;
      }
    }
    .iam {
      display: flex;
      margin-top: 20px;
      font-size: 20px;
      color: #999;
      div:nth-of-type(1) {
        flex: 1;
        font-size: 20px;
      }
      div:nth-of-type(2) {
        flex: 0 0 120px;
        text-align: right;
        font-style: italic;
        color: #de2c2d;
        font-size: 30px;
        span{
          font-size: 20px;
        }
      }
    }
  }
}
</style>



